* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}
.previewpagelinks {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(255,255,255,0.702);
  padding: 0.5rem;
}
.previewpagelinks a {
  width: 100%;
  color: #000;
  font-size: 1rem;
  margin: 0 0 0.5rem;
}
body {
  background-color: #fff;
  font-size: 1rem;
  line-height: normal;
  color: #000;
  font-family: Arial, Helvetica, sans-serif;
  overflow-x: hidden;
  width: 100%;
}
header {
  box-shadow: 0 3px 6px rgba(0,0,0,0.16);
  width: 100%;
  background: #fff;
}
header .container {
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
  padding: 1.5rem 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header .container .home {
  padding: 0;
}
a,
button,
input {
  cursor: pointer;
}
button.read {
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: 0;
  padding: 0;
  color: #c69ccc;
  font-size: 18px;
  font-weight: 400;
  margin: auto;
  pointer-events: none;
}
button.read:hover {
  opacity: 0.75;
}
input {
  -webkit-appearance: textfield;
  outline-offset: 0;
}
section {
  padding: 2rem 0;
}
section.news {
  background-image: url("../img/static/bgLayer.svg");
  background-position: center center;
  background-size: inherit;
  background-repeat: no-repeat;
}
section.news.v2 {
  background-color: rgba(198,156,204,0.2);
  margin-bottom: 1rem;
}
section.news.v2 .link {
  display: flex;
}
section.news .link {
  margin: 4rem auto 0;
}
section.requirement {
  background-color: #c69ccc;
  background-image: url("../img/static/owl_opacity.svg");
  background-position: -10% bottom;
  background-size: inherit;
  background-repeat: no-repeat;
  padding: 3rem 0;
}
section.how_to_get_status {
  background-color: #00c9d2;
  background-image: url("../img/static/owl-opacity-v2.svg");
  background-position: right bottom;
  background-size: inherit;
  background-repeat: no-repeat;
  padding: 3rem 0;
}
section.doubleDiv_block {
  margin: 2rem 0;
}
section.doubleDiv_block .container {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: calc(100% * 3 / 8) 30px 1fr;
  grid-template-columns: calc(100% * 3 / 8) 1fr;
  -ms-grid-rows: 1fr;
  grid-template-rows: 1fr;
  grid-column-gap: 30px;
}
section.doubleDiv_block .container > *:nth-child(1) {
  -ms-grid-row: 1;
  -ms-grid-column: 1;
  position: relative;
}
section.doubleDiv_block .container > *:nth-child(2) {
  -ms-grid-row: 1;
  -ms-grid-column: 3;
}
.hamburger {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 0.25rem;
  border: 1px solid #707070;
  color: #707070;
  background-color: #fff;
  font-size: 1.75rem;
  display: none;
  align-items: center;
  justify-content: center;
  transition: 0.125s ease-in-out;
}
.hamburger:hover {
  -webkit-box-shadow: 0px 0px 5px 0px #00c9d2;
  -moz-box-shadow: 0px 0px 5px 0px #00c9d2;
  box-shadow: 0px 0px 5px 0px #00c9d2;
}
.hamburger:active {
  -webkit-box-shadow: inset 0px 0px 5px 0px #00c9d2;
  -moz-box-shadow: inset 0px 0px 5px 0px #00c9d2;
  box-shadow: inset 0px 0px 5px 0px #00c9d2;
}
h1 {
  font-size: 5.625rem;
}
h2 {
  font-size: 50px;
}
h2.section-title {
