}
/* include libs styles*/
/* include layouts styles*/
.menu {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.menu ul {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.menu ul > :first-child {
  margin-left: 0;
}
.menu ul li {
  margin-left: 1.5em;
}
.menu ul li a {
  color: #707070;
  font-family: 'Segoe UI Regular';
  font-size: 20px;
  font-weight: 400;
  position: relative;
}
.menu ul li a.active:before {
  height: 4px;
  background-color: #c69ccc;
  width: calc(100% - 2.25rem);
  position: absolute;
  content: '';
  left: 50%;
  transform: translateX(-50%);
  bottom: -1rem;
}
.menu ul li a:hover {
  color: #000;
  opacity: 1;
}
@media (max-width: 1279px) {
  header {
    padding: 15px;
  }
  header .container {
    padding: 0;
    flex-wrap: wrap;
  }
  .menu {
    width: 100%;
    height: 0rem;
    overflow: hidden;
    position: relative;
    flex-shrink: 0;
    background-color: #fff;
    z-index: 1;
    transition: 0.25s ease-in-out;
  }
  .menu.show {
    height: 15rem;
  }
  .menu ul {
    flex-direction: column;
    width: 100%;
  }
  .menu ul li {
    margin: 0;
    width: 100%;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .menu ul li a {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
  }
  .menu ul li a.active:before {
    bottom: 0;
    height: 100%;
    z-index: -1;
    opacity: 0.5;
  }
}
.topSlider {
  background-image: url("../img/static/vincent-van-zalinge-rbaU7FIad-w-unsplash.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
.topSlider .slide .container {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  max-width: 960px;
  padding: 0.5rem 15px;
  margin: 0 auto;
}
