.tab-content {
  position: relative;
  overflow: hidden;
}
.tab-content > div:last-child {
  height: 0;
  overflow: hidden;
  opacity: 0;
  z-index: -1;
}
.tab-content > div:last-child.show {
  height: auto;
  opacity: 1;
  z-index: 0;
}
.tab-content > div:first-child {
  height: 0;
  overflow: hidden;
  opacity: 0;
