a {
  text-decoration: none;
  color: inherit;
  padding: 0 1.125rem;
  box-sizing: border-box;
}
a.add_member {
  display: flex;
  align-items: center;
  padding: 0;
  color: #c69ccc;
}
a.add_member:before {
  position: relative;
  color: #c69ccc;
  font-size: 33px;
  font-weight: 400;
  margin-right: 11px;
}
a.add_member:after {
  font-size: 14px;
  font-weight: 400;
  position: relative;
  line-height: normal;
  font-family: Arial, Helvetica, sans-serif;
  content: 'Добавить участника';
  padding-bottom: 4px;
  border-bottom: 1px dashed #c69ccc;
}
a.zip,
a.pdf {
  color: #3e5259;
  font-size: 12px;
  font-weight: 400;
  line-height: 1;
  width: 100px;
  height: 2rem;
  border-radius: 0.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid #c69ccc;
  background-color: rgba(198,156,204,0.4);
  transition: 0.125s ease-in-out;
  padding: 0 0.25rem;
  margin: 0.5rem;
  white-space: pre;
}
a.zip:active,
a.pdf:active {
  opacity: 1;
}
a:hover {
  opacity: 0.75;
}
a.link {
  color: #fff;
  font-family: 'Segoe UI Regular';
  font-size: 18px;
  font-weight: 400;
  text-transform: uppercase;
  border: 3px solid #fff;
  height: 58px;
  min-width: 180px;
  width: auto;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
a.v1 {
  background-color: transparent;
}
a.v1:hover {
  backdrop-filter: none;
  background-color: #00c9d2;
  text-decoration: none;
  color: #fff;
  opacity: 1;
}
a.v2 {
  background-color: #00c9d2;
  max-width: 11.25rem;
}
a.v2:hover {
  backdrop-filter: none;
  background-color: #c69ccc;
  text-decoration: none;
  color: #fff;
}
a.v3 {
  background-color: #c69ccc;
}
a.v3:hover {
  backdrop-filter: none;
  background-color: #00c9d2;
  text-decoration: none;
  color: #fff;
}
a.v4 {
  border: 3px solid transparent;
  background-color: transparent;
}
